.maintain-panel {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  color: white;
  font-family: monospace;
  font-size: 90%;
}

.maintain-panel:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  content: ' ';
  background: linear-gradient(115deg, black -50%, transparent);
  z-index: -1;
}

.maintain-panel .maintain-panel-title {
  font-size: 200%;
}

.maintain-panel .apply-button {
  margin: 0.5em;
}